.btn-login-primary {
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: #d92c3e;
  --bs-btn-border-color: #d92c3e;
  --bs-btn-border-radius: .5rem;
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: rgb(187 22 39);
  --bs-btn-hover-border-color: rgb(175, 18, 33);
  --bs-btn-focus-shadow-rgb: 217, 44, 62;
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: rgb(181, 20, 36);
  --bs-btn-active-border-color: rgb(175, 18, 33);
  --bs-btn-disabled-color: var(--bs-white);
  --bs-btn-disabled-bg: #d92c3e;
  --bs-btn-disabled-border-color: #d92c3e;
}

.btn-login-outline-secondary {
  --bs-btn-color: #2d3850;
  --bs-btn-border-color: #2d3850;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #2d3850;
  --bs-btn-hover-border-color: #2d3850;
  --bs-btn-focus-shadow-rgb: 108,117,125;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #2d3850;
  --bs-btn-active-border-color: #2d3850;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #2d3850;
  --bs-btn-disabled-bg: transparent;
  --bs-btn-disabled-border-color: #2d3850;
  --bs-gradient: none;
}

.form-login-check-input:checked {
  background-color: #d92c3e;
  border-color: #d92c3e;
}

.form-login-check-input:focus {
  border-color: #f07d89;
  outline: 0;
  box-shadow: none;
}